<template>
  <div>
    <el-dialog :title="$t('order.add')" :visible.sync="addVisible" @close="reset()" width="446px">
      <div class="small_form shelfconfig_small">
        <el-form ref="orderform" label-width="100px" :rules="rules" :model="orderform">
          <el-form-item :label="$t('order.order_name')" prop="order_name">
            <el-input v-model="orderform.order_name"></el-input>
          </el-form-item>
          <el-form-item :label="$t('order.quantity')" prop="order_num">
            <el-input v-model="orderform.order_num"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="cancel(orderform)">{{$t('common.cancel')}}</el-button>
            <el-button type="primary" @click="save(orderform)">{{$t('common.save')}}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {
    newWorkOrder
  } from '@/request/api/order'
  export default {
    data() {
      return {
        id: '',
        addVisible: false,
        orderform: {
          order_name: '',
          order_num: '',
        },
        rules: {
          order_name: [{
              required: true,
              message: this.$t('order.ordernamehoulder'),
              trigger: 'blur'
            },
            {
              validator: (rule, value, callback) => {
                if (/[\u4e00-\u9fa5_a-zA-Z0-9_]{2,16}/.test(value) == false) {
                  callback(new Error(this.$t('order.orderreg')));
                } else {
                  callback();
                }
              },
              trigger: 'blur'
            }
          ],
          order_num: [{
              required: true,
              message: this.$t('order.quantityhoulder'),
              trigger: 'blur'
            },
            {
              validator: (rule, value, callback) => {
                if (/^[0-9]*$/.test(value) == false) {
                  callback(new Error(this.$t('order.numreg')));
                } else {
                  callback();
                }
              },
              trigger: 'blur'
            }
          ]
        }

      }

    },
    methods: {
      open(id) {
        this.id = id
        this.addVisible = true
      },
      reset() {
        this.$refs.orderform.resetFields()
      },
      cancel(orderform) {
        this.$refs.orderform.resetFields()
        this.addVisible = false
      },
      save(orderform) {
        this.$refs.orderform.validate((valid) => {
          if (valid) {
            newWorkOrder({
              file_id: this.id,
              order_name: this.orderform.order_name,
              order_num: this.orderform.order_num
            }).then(res => {
              if (res.result) {
                this.addVisible = false
                this.$message({
                  showClose: true,
                  message: this.$t('order.addordersuccess'),
                  type: 'success'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: res.msg,
                  type: 'error'
                })
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style>
</style>



