<template>
  <div>
    <search-file @search="getFiles"></search-file>
    <add-order ref="addorder"></add-order>
    <el-table :data="feederList" :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :empty-text="$t('common.nodata')"
      border>
      <el-table-column :show-overflow-tooltip='true' prop="id" :label="$t('order.feederid')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="upload_order_code" :label="$t('order.order_name')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="show_name" :label="$t('order.filename')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="upload_time" :label="$t('order.uploadtime')">
      </el-table-column>
      <el-table-column :label="$t('common.operation')">
        <template slot-scope="scope">
          <el-button type="success" size="mini" @click="openAdd(scope.row.id)">{{$t('order.add')}}</el-button>
          <el-button type="danger" size="mini" @click="deleteFile(scope.row.id)">{{$t('common.delete')}}</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import {
    filesShow,
    delFile
  } from '@/request/api/file.js'
  import SearchFile from '../commonchild/searchfile.vue'
  import AddOrder from '../commonchild/addorder'
  export default {
    components: {
      SearchFile,
      AddOrder
    },
    data() {
      return {
        feederList: [],
      }
    },
    mounted() {
      this.getFiles()
    },
    methods: {
      getFiles(show_name, order_code) {
        filesShow({
          file_type: 1,
          show_name: show_name,
          order_code: order_code
        }).then(res => {
          if (res.result) {
            this.feederList = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      openAdd(id) {
        this.$refs.addorder.open(id)
      },
      deleteFile(id) {
        this.$confirm(this.$t('order.deletetips'), this.$t('order.tips'), {
          confirmButtonText: this.$t('common.sure'),
          cancelButtonText: this.$t('common.cancel'),
          type: 'warning'
        }).then(() => {
          delFile({
            id: id
          }).then(res => {
            if (res.result) {
              this.$message({
                showClose: true,
                message: this.$t('common.deleteSuccess'),
                type: 'success'
              })
              this.getFiles()
            } else {
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        })
      }
    }
  }
</script>

<style>
</style>



