<template>
  <div class="file_search">
    <div class="file_item_div">
      <span class="file_item_name">文件名</span>
      <el-input v-model="show_name" clearable></el-input>
    </div>
    <div class="file_item_div">
      <span class="file_item_name">文件所属工单</span>
      <el-input v-model="order_code" clearable></el-input>
    </div>
    <div class="file_item_div">
      <el-button type="primary" @click="search" size="mini">{{$t('common.search')}}</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        show_name: '',
        order_code: '',
      }
    },
    methods: {
      search() {
        this.$emit('search', this.show_name, this.order_code)
      }
    }

  }
</script>

<style>
  .file_search {
    font-size: 14px;
    display: flex;
    height: 40px;
    line-height: 40px;
    padding: 10px 20px;
  }

  .file_item_div {
    display: flex;
    margin-right: 20px;
  }

  .file_item_div .el-input {
    width: 160px;
  }

  .file_item_div .el-input__inner {
    width: 160px;
  }

  .file_item_name {
    display: inline-block;
    padding-right: 10px;
  }
</style>



